@import "../_sass/fonts";
@import "../_sass/settings";
@import "../_sass/syntax-highlighting";

* {
    margin: 0;
    padding: 0;
}

.group:after {
    content: "";
    display: table;
    clear: both;
}

html, body {
    height: 100%; 
}

html {
    text-align: baseline;
    font-size: 11.0pt;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


body {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 13%;
    font-family: RobotoCondensed;
    background-color: $bg-color;
    color: $text-color;
    max-width: 1400px;
    counter-reset: sidenote-counter;
    line-height: 0.0;
    // text-align: justify;
    // text-justify: inter-word;
}

p, a {
    font-size: 1rem;
}


h1 {
    font-weight: 200;
    font-family: StudioFeixenSans;
    font-weight: bold;
    margin-top: 2.5rem;
    margin-top: 1.5cm;
    margin-bottom: 1.568rem;
    font-size: 1.8rem;
    line-height: 0.784;
}

h1 code {
    font-family: OperatorMono;
}

h2, h3 {
    font-family: StudioFeixenSans;
    font-weight: bold;
    margin-top: 3rem;
    margin-bottom: -0.5rem;
}

h2 {
    font-size: 1.4rem;
    line-height: 0.933333333333333;
}

h3 { 
    font-size: 1.2rem;
    line-height: 1.08888888888889; }

.MathJax_Display {
    // HTML-CSS mathjax processer
    font-size: 1.1rem !important;
    text-align: left !important;
    color: #000000 !important;
    margin-left: 1.5cm !important;
    margin-right: auto !important;
    width: 40% !important;
}

.MathJax_CHTML {
    // CommonHTML mathjax processer
    color: #000000 !important;
    font-weight: 500 !important;
    font-size: 1.1rem !important;
}

a {
    color: $contrast-color;
    text-decoration: none;
}

p > a, cite > a, span > a {
    @if $link-style == underline {
        color: $text-color;
        text-decoration: none;
        background: -webkit-linear-gradient($bg-color, $bg-color), -webkit-linear-gradient($bg-color, $bg-color), -webkit-linear-gradient(currentColor, currentColor);
        background: linear-gradient($bg-color, $bg-color), linear-gradient($bg-color, $bg-color), linear-gradient(currentColor, currentColor);
        -webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
        -moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
        background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
        background-repeat: no-repeat, no-repeat, repeat-x;
        text-shadow: 0.03em 0 $bg-color, -0.03em 0 $bg-color, 0 0.03em $bg-color, 0 -0.03em $bg-color, 0.06em 0 $bg-color, -0.06em 0 $bg-color, 0.09em 0 $bg-color, -0.09em 0 $bg-color, 0.12em 0 $bg-color, -0.12em 0 $bg-color, 0.15em 0 $bg-color, -0.15em 0 $bg-color;
        background-position: 0% 93%, 100% 93%, 0% 93%; 
    }
    @else {
        color: $contrast-color;
        text-decoration: none;
    }
  }


p .subtitle {
    font-style: italic;
    margin-top: 2.1777777777777778rem;
    margin-bottom: 2.1777777777777778rem;
    font-size: 1.8rem;
    display: block;
    line-height: 1.08888888888889;
}

p, li {
    line-height: 1.5rem;
    margin-top: 1.4rem;
    padding-right: 2rem;
    vertical-align: baseline;
}

/* Chapter Epigraphs */
div.epigraph {
    margin: 3.5636rem 0;
}

div.epigraph > blockquote {
    margin-top: 3rem;
    margin-bottom: 3rem;
}

div.epigraph > blockquote, div.epigraph > blockquote > p {
    font-style: italic;
}

div.epigraph > blockquote > footer {
    font-style: normal;
    width: 45%;
    padding-left: 2.5%;
    padding-right: 2.5%;
    padding-top: 0;
    margin-top: 0.4455rem;
    margin-bottom: 3.1182rem;
}

div.epigraph > blockquote > footer > cite {
    font-style: italic;
}
/* end chapter epigraphs styles */

blockquote p, blockquote footer {
    width: 45%;
    padding-left: 2.5%;
    padding-right: 2.5%;
}

blockquote footer {
    font-size: 1.1rem;
    line-height: 1.78181818;
    margin-top: 1.78181818rem;
    margin-bottom: 1.78181818rem;
    text-align: right;
}


pre, pre code, p code, p pre code, .marginnote code, figcaption.maincolumn-figure code {
    // Inline code 
    font-family: OperatorMono;
    font-size: 0.9rem;
    color: $code-color;
    background-color: #eeeff0;
    padding-left: 6px;
    padding-right: 6px;
    border-radius: 5px;
}

pre code {
    // Code blocks
    font-size: 0.8rem;
    margin-left: -2rem;
    line-height: 1.6;
    padding: 0.5rem 1rem 0.5rem 1rem;
    margin-top: 1rem;
    margin-bottom: 0rem;
    color: $code-color;
    display: block;
    background: $bg-color;
    background-color: contrast-color;
    
}

li code {
    font-family: OperatorMono;
}

table.norulers {
    width: auto;
    width: 53%;
    border-spacing: 0.1rem;
    margin-bottom: 2rem;
    margin-left: 2rem;
    margin-top: 0.5cm;
    border-spacing: 0px -5px;
    border-top: 0px solid $border-color;
    border-bottom: 0px solid $border-color;
    font-family: RobotoCondensed;
}

table.norulers td {
    font-family: RobotoCondensed;
    border-bottom: 0px solid $border-color;
}

table.norulers tr em {
    font-size: 1.0rem;
}

table.norulers th {
    font-size: 1.1rem;
    font-family: StudioFeixenSans;
    font-weight: bold;
    border-bottom: 0px solid $border-color;
}


table, table.booktabs {
    width: auto;
    border-spacing: 0px;
    margin-bottom: 2rem;
    font-size: 17px;
    border-top: 1.5px solid $border-color;
    border-bottom: 1.5px solid $border-color;
}

.booktabs th, th {
    font-family: Lato,SegoeUI;
    font-size: 1rem;
    font-weight: normal;
    padding-bottom: 0.95ex;
    padding-top: 0.95ex;
}


th, td {
    line-height: 1.71428571;
    font-family: SegoeUI;
    padding-left: 1.0em;
    padding-right: 1.0em;
    // border-bottom: 1px solid #ddd;
    font-size: 0.9rem;
}

th code, td code {
    font-family: OperatorMono;
}

.booktabs th.cmid, th {
    text-align: center;
    border-bottom: 1px solid lighten($border-color, 25%);
}

.booktabs th.cmid {
    border-bottom-length: 50px;
}

.booktabs th.nocmid {
    border-bottom: none;
}

.booktabs tbody tr:first-child td,  tr:first-child td {
    padding-top: 0.95ex; /* add space between thead row and tbody */
} 

.booktabs tbody tr:last-child td,  tr:last-child td {
    padding-bottom: 0.95ex; /* add space between thead row and tbody */
} 

.booktabs td, td {
    padding-left: 1.0em;
    padding-right: 1.0em;
    text-align: left;
}            

.booktabs caption, caption {
    font-size: 90%;
    text-align: left;
    width: auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1ex;
    caption-side: top;
}

.booktabs tbody tr, .booktabs thead tr th.l, .booktabs td.l { text-align: left !important; }
.booktabs tbody tr, .booktabs thead tr th.c, .booktabs td.c { text-align: center !important; }
.booktabs tbody tr, .booktabs thead tr th.r, .booktabs td.r { text-align: right !important; }

.table-caption {
    float:right;
    clear:right;
    margin-right: -60%;
    width: 50%;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 1.0rem;
    line-height: 1.96;
}
/* -- End of Table Styling section --*/

/* Basic Layout stuff --*/

article {
    position: relative;
    padding: 1rem 0rem 2.5rem 0rem;
}

section {
    padding-top: 1rem;
    padding-bottom: 1rem;
}

ul,ol { width: 45%;
     -webkit-padding-start: 5%;
     -webkit-padding-end: 5%;
     list-style-type: none; }

//li { padding: 0.5em 0; } //vertical padding on list items screws up vertical rhythym

figure, figure img.maincolumn {
    max-width: 55%;
    -webkit-margin-start: 0;
    -webkit-margin-end: 0;
    margin-bottom: 3em;
    margin-top: 2em;
}

figcaption {
    float: right;
    clear: right;
    margin-right: -50%;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 1.0rem;
    line-height: 1.6;
    vertical-align: baseline;
    position: relative;
    max-width: 40%;
}
             
figure.fullwidth figcaption {
    float: left;
    margin-right: 0%;
    margin-left: 33%;
    max-width: 100%;
}

img { max-width: 100%; }

figcaption.maincolumn-figure {
    max-width: 50%;
    margin-right: -60%;
}

.sidenote, .marginnote {
    float: right;
    clear: right;
    margin-right: -60%;
    width: 50%;
    margin-top: 0;
    margin-bottom: 1.96rem;
    font-size: 0.8rem;
    vertical-align: baseline;
    position: relative;
}

.marginnote code {
    font-size: 0.8rem !important;
}

.marginnote a {
    font-size: 0.8rem;
}


li .sidenote, li .marginnote{ margin-right: -80%; } //added to allow for the fact that lists are indented and marginnotes and sidenotes push to right

blockquote .sidenote, blockquote .marginnote { margin-right: -79% }

.sidenote-number { counter-increment: sidenote-counter; }

.sidenote-number:after, .sidenote:before { content: counter(sidenote-counter) " ";
                                           font-family: et-bembo-roman-old-style;
                                           color: $contrast-color; //added color
                                           position: relative;
                                           vertical-align: baseline; }

.sidenote-number:after { content: counter(sidenote-counter);
                         font-size: 1rem;
                         top: -0.5rem;
                         left: 0.1rem; }

.sidenote:before { content: counter(sidenote-counter) ".\000a0"; // this is unicode for a non-breaking space
                   color: $contrast-color;
                   top: 0rem; } //removed superscripting for numerical reference in sidenote

p, footer, div.table-wrapper,  .MJXc-display { width: 55%; }

div.table-wrapper, table { overflow-x: auto; } //changed all overflow values to 'auto' so scroll bars appear only as needed

@media screen and (max-width: 760px) { p, footer { width: 90%; }
                                       pre code { width: 87.5%; }
                                       ul { width: 85%; }
                                       figure { max-width: 90%; }
                                       figcaption, figure.fullwidth figcaption { margin-right: 0%;
                                                                                 max-width: none; }
                                       blockquote p, blockquote footer, div.epigraph > blockquote > footer,.MJXc-display {  width: 80%;
                                                                          padding-left: 5%;
                                                                          padding-right: 5%;
                                                                        }}

.marginnote code, .sidenote code { font-size: 1rem; } //more .code class removal

pre { width: 52.5%; //, pre code, p pre code are both selectors in tufte.css - they have been removed here to fix alignment issues
           padding-left: 2.5%;
           overflow-x: auto; }

.fullwidth, li.listing div{ max-width: 90%; }

.full-width { .sidenote, .sidenote-number, .marginnote { display: none; } }

span.newthought { font-variant: small-caps;
                  font-size: 1.2em;
                  letter-spacing: 0.05rem; }

input.margin-toggle { display: none; }

label.sidenote-number { display: inline; }

label.margin-toggle:not(.sidenote-number) { display: none; }

@media (max-width: 760px) {
    label.margin-toggle:not(.sidenote-number) {
        display: inline;
        color: $contrast-color;
    }
    .sidenote, .marginnote {
        display: none;
    }
    .margin-toggle:checked + .sidenote, .margin-toggle:checked + .marginnote {
        display: block;
        float: left;
        left: 1rem;
        clear: both;
        width: 95%;
        margin: 1rem 2.5%;
        vertical-align: baseline;
        position: relative;
    }
    label {
        cursor: pointer;
    }
    pre, pre code, p code, p pre code {
        width: 90%;
        padding: 0;
    }
    .table-caption {
        display: block;
        float: right;
        clear: both;
        width: 98%;
        margin-top: 1rem;
        margin-bottom: 0.5rem;
        margin-left: 1%;
        margin-right: 1%;
        vertical-align: baseline;
        position: relative;
    }
    div.table-wrapper, table, table.booktabs {
        width: 85%;
    }
    div.table-wrapper {
        border-right: 1px solid #efefef;
    }
    img {
        max-width: 100%;
    } 
    figcaption.maincolumn-figure, figure.fullwidth figcaption {
        float:none;
        text-align: center;
    }
    figure.fullwidth figcaption {
        margin-left: 0;
    }
}

figcaption.maincolumn-figure, figcaption.maincolumn-figure a, figcaption.maincolumn-figure code {
    font-size: 0.8rem;
}


/*--- End of Basic Layout stuff from tufte.css ---*/

.contrast {
    color: $contrast-color;
}
.smaller {
    font-size: 0.9rem;
    font-family: Lato,SegoeUI;
    font-style: normal;
}

