    border-width:1px 0;
    list-style:none;
    margin:0;
    padding:0;
    text-align:center;
}
.nav li{
    display:inline;
    text-align:center;
}
.nav a {
    margin-top: 1rem;
    display:inline-block;
    padding:10px;
    // font-size: 1.2rem;
    font-family: Minion;
    font-style: italic;
    font-size: 1.0rem;
    // margin-left: auto;
    // margin-right: auto;
}

ul.footer-links, .credits{
  list-style: none;
  text-align: center;
  margin: 0 auto;
}

ul.footer-links li{
  display: inline;
  padding: 0.5rem 0.25rem;
}

.credits{
  padding: 1rem 0rem;
}

//change font color for credit links in footer

.credits{
  font-family: $sans-font;
    & a{
      color: $contrast-color;
    }
}

// End of Nav and Footer styling area

//Full width page styling stuff

.hangingindent {
    // padding-left: 22px ;
    text-indent: -22px ;
    margin-left: auto;
    margin-right: auto;
    width: 65% !important;
    padding-right: 11%;
    margin-bottom: -0.8rem;
    // text-align: justify;
    // text-justify: inter-word;
}


body.full-width {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    // padding: 0% 0%;

}

.content-listing, ul.content-listing li.listing {
    width: 83%;
    margin-left: auto;
    margin-right: auto;
}

.full-width article p {
    width: 89%;
    font-family: RobotoCondensed;
    font-style: normal;
}

h1.content-listing-header{
    width: 83%;
    margin-left: auto;
    margin-right: auto;
    font-style: italic;
    letter-spacing: 0.1rem;
    text-align: center;
}

li.listing hr{
    width: 100%;
}
.listing, .listing h3 {
    display: inline-block;
    margin: 0;
    font-family: Minion;
    font-style: italic;
    font-weight: normal;
    font-size: 1.6rem;
}

li.listing {
  margin: 0;
  & p{
    width: 111%;
    margin-bottom: 0.8cm;
    margin-top: 0.1cm;
  }
}

li.listing:last-of-type{
  border-bottom: none;
  margin-bottom: 1.4rem;
}

li.listing h3.new {
  text-transform: uppercase;
  font-style: normal;
}

hr.slender {
    border: 0;
    height: 1px;
    margin-top: 0.6rem;
    margin-bottom: 0.6rem;
    color: $bg-color;
    // background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    // background-image:    -moz-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    // background-image:     -ms-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    // background-image:      -o-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
}
// End of front listing page stuff


// Printing ands screen media queries

// Does not display a print-footer for screen display
@media screen{
  .print-footer{
    display: none;
  }
}

//printing stuff
@media print {

    * {
    -webkit-transition: none !important;
    transition: none !important;
    }
    *,
    *:before,
    *:after {
        background: transparent !important;
        box-shadow: none !important;
        text-shadow: none !important;
    }
    @page {
        margin: 0.75in 0.5in 0.75in 0.5in;
        orphans:4; widows:2;
    }

    body {
        font-size:  12pt;

    }
    html body span.print-footer{
      font-family: $sans-font;
      font-size: 9pt;
      margin-top: 22.4pt;
      padding-top: 4pt;
      border-top: 1px solid #000;
    }

    thead {
        display: table-header-group;
    }

    tr,
    img {
        page-break-inside: avoid;
    }

    img {
        max-width: 100% !important;
    }

    p,
    h2,
    h3 {
        orphans: 4;
        widows: 4;
    }
    article h2, article h2 h3, article h3, article h3 h4, article h4, article h4 h5 {
        page-break-after: avoid;
    }

    body header , body footer {
      display:none;
    }
}
